.widgetSm {
    flex: 2;
    padding: 20px;
    margin-right: 20px;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    width: 50%;
}

.widgetSmTitle {
    font-size: 22px;
    font-weight: 600;
}

.widgetSmList {
    margin: 0;
    padding: 0;
    list-style: none;
}

.widgetSmListItem {
    display: flex;
    justify-content:space-between ;
    /* align-items: center; */
    margin: 20px 0px;
}

.widgetSmImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.widgetSmUser {
    display: flex;
    flex-direction: column;
    align-items: left;
}

.widgetSmUserName {
    font-weight: 600;
    align-items: left;
}

.widgetSmUserTitle {
    font-weight: 300;
    color: #555;
}

.widgetSmButton {
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    padding: 7px 10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;
}