.topbar {
    width: 100%;
    height: 50px;
    background-color: bisque;
    position:sticky;
    top: 0;
    z-index: 999;
}

.topbarWrapper {
    height: 100%;
    padding: 0px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-weight: bold;
    font-size: 30px;
    cursor: pointer;
    color:cadetblue    
}

.topRight {
    display: flex;
    align-items: center;
    /* justify-content: space-around; */
    /* padding: 5px */
}

.topbarIconContainer {
    position: relative;
    cursor: pointer;
    margin-right: 10px;
    color:violet
}

.topIconBadge {
    width: 15px;
    height: 15px;
    position: absolute;
    top: -5px;
    right: 0;
    background-color: chocolate;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    border-radius: 50%
}

.topAvatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
}